import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { ModernTable } from "@/components/ModernTable";
import Header from "@/components/Header";
import { 
    Plus, 
    Workflow, 
    Eye, 
    Edit, 
    Trash, 
    Activity, 
    Clock, 
    Play, 
    CheckCircle2, 
    AlertCircle,
    X
} from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { 
    Dialog, 
    DialogContent, 
    DialogHeader, 
    DialogTitle, 
    DialogFooter, 
    DialogDescription 
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";

const WorkflowLayout = () => {
    const navigate = useNavigate();
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [workflowName, setWorkflowName] = useState("");
    const [isLoading, setIsLoading] = useState(false);

    // Mock data for workflows
    const [workflows] = useState([
        {
            id: "wf-1",
            name: "Gmail to Leads Automation",
            type: "Gmail Integration",
            status: "active",
            lastRun: "2 mins ago",
            created: "Oct 24, 2023"
        },
        {
            id: "wf-2",
            name: "Lead Follow-up Campaign",
            type: "Campaign Launch",
            status: "paused",
            lastRun: "1 day ago",
            created: "Oct 20, 2023"
        },
        {
            id: "wf-3",
            name: "New Lead Agent Creation",
            type: "Agent Creation",
            status: "error",
            lastRun: "5 mins ago",
            created: "Oct 15, 2023"
        }
    ]);

    const handleCreateWorkflow = () => {
        if (!workflowName.trim()) return;
        setIsModalOpen(false);
        // Simulate creation and navigate to canvas
        navigate(`/workflows/create?name=${encodeURIComponent(workflowName)}`);
    };

    const workflowActions = [
        {
            label: "View Canvas",
            icon: <Eye className="h-4 w-4" />,
            onClick: (item) => navigate(`/workflows/create?id=${item.id}`),
        },
        {
            label: "Edit Settings",
            icon: <Edit className="h-4 w-4" />,
            onClick: (item) => console.log("Edit settings", item),
        },
        {
            label: "Delete Workflow",
            icon: <Trash className="h-4 w-4" />,
            onClick: (item) => console.log("Delete workflow", item),
        },
    ];

    const getStatusIcon = (status) => {
        switch (status) {
            case 'active': return <CheckCircle2 className="h-4 w-4 text-emerald-400" />;
            case 'paused': return <Clock className="h-4 w-4 text-amber-400" />;
            case 'error': return <AlertCircle className="h-4 w-4 text-rose-400" />;
            default: return null;
        }
    };

    return (
        <div className="app-shell-bg min-h-screen py-8 px-8 font-sans">
            <div className="max-w-[1600px] mx-auto space-y-6">
                <Header
                    title="Workflows"
                    buttonText="Workflow"
                    action={() => setIsModalOpen(true)}
                    filterByName={true}
                    useSheet={false}
                    className="bg-transparent border-b border-white/10 pb-6"
                    titleClassName="text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-white to-white/60"
                />

                <div className="flex items-center space-x-2">
                    <Badge variant="outline" className="px-3 py-1 bg-white/5 border-white/10 text-white/80 hover:bg-white/10 transition-colors cursor-default">
                        <Activity className="h-3 w-3 mr-1" />
                        <span>{workflows.length} Active Automations</span>
                    </Badge>
                </div>

                <div className="neo-card w-full overflow-hidden p-1">
                    <div className="overflow-x-auto">
                        <ModernTable
                            data={workflows}
                            isLoading={isLoading}
                            isSelectable={true}
                            showColumnSelection={true}
                            headerClassName="bg-white/5 border-b border-white/10 text-white/70"
                            rowClassName="hover:bg-white/5 border-b border-white/5 text-white/90"
                            cellClassName="text-white/80"
                            columns={[
                                {
                                    header: "Workflow Name",
                                    accessor: "name",
                                    className: "min-w-[250px]",
                                    cell: (value, item) => (
                                        <div className="flex items-center space-x-3">
                                            <div className="h-9 w-9 rounded-lg bg-indigo-500/20 flex items-center justify-center border border-indigo-500/30">
                                                <Workflow className="h-5 w-5 text-indigo-400" />
                                            </div>
                                            <div>
                                                <div className="font-medium text-white">{String(value)}</div>
                                                <div className="text-xs text-white/40">ID: {item.id}</div>
                                            </div>
                                        </div>
                                    ),
                                    sortable: true,
                                },
                                {
                                    header: "Type",
                                    accessor: "type",
                                    className: "min-w-[150px]",
                                    cell: (value) => (
                                        <Badge variant="secondary" className="bg-white/5 text-white/70 border-white/10 font-normal">
                                            {String(value)}
                                        </Badge>
                                    ),
                                },
                                {
                                    header: "Status",
                                    accessor: "status",
                                    className: "min-w-[120px]",
                                    cell: (value) => (
                                        <div className="flex items-center space-x-2 capitalize">
                                            {getStatusIcon(value)}
                                            <span>{String(value)}</span>
                                        </div>
                                    ),
                                },
                                {
                                    header: "Last Run",
                                    accessor: "lastRun",
                                    className: "min-w-[150px]",
                                    cell: (value) => (
                                        <div className="flex items-center text-white/60">
                                            <Play className="h-3 w-3 mr-2 opacity-50" />
                                            {String(value)}
                                        </div>
                                    ),
                                },
                                {
                                    header: "Created At",
                                    accessor: "created",
                                    className: "min-w-[150px]",
                                    sortable: true,
                                },
                            ]}
                            actions={workflowActions}
                            emptyState={
                                <div className="text-center p-12">
                                    <div className="h-16 w-16 bg-white/5 rounded-full flex items-center justify-center mx-auto mb-4 ring-1 ring-white/10 shadow-[0_0_15px_rgba(255,255,255,0.05)]">
                                        <Workflow className="h-8 w-8 text-white/40" />
                                    </div>
                                    <h3 className="text-xl font-medium mb-2 text-white/90">No workflows found</h3>
                                    <p className="text-white/50 mb-8 max-w-sm mx-auto">
                                        Automate your business processes by creating your first workflow.
                                    </p>
                                    <button
                                        className="neo-btn flex items-center justify-center mx-auto"
                                        onClick={() => setIsModalOpen(true)}
                                    >
                                        <Plus className="h-4 w-4 mr-2" />
                                        Create Workflow
                                    </button>
                                </div>
                            }
                        />
                    </div>
                </div>
            </div>

            {/* Create Workflow Modal */}
            <Dialog open={isModalOpen} onOpenChange={setIsModalOpen}>
                <DialogContent className="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 neo-card bg-[#0b0f2e]/98 backdrop-blur-2xl border-indigo-500/20 text-white sm:max-w-[420px] p-0 overflow-hidden shadow-[0_0_80px_rgba(79,70,229,0.15)] z-[100]">
                    <div className="h-1.5 w-full bg-gradient-to-r from-indigo-600 via-indigo-400 to-indigo-600 animate-pulse" />
                    
                    <div className="p-8 space-y-8">
                        <DialogHeader className="space-y-2">
                            <div className="flex items-center space-x-2">
                                <span className="text-[10px] font-black text-indigo-400 uppercase tracking-[0.3em]">System Architect</span>
                                <div className="h-px flex-1 bg-white/5" />
                            </div>
                            <DialogTitle className="text-lg font-semibold tracking-tight text-white leading-none">
                                New Architecture Initialization
                            </DialogTitle>
                            <DialogDescription className="text-[12px] text-white/40 pt-1 leading-relaxed">
                                Define the primary identity for this automation sequence. This signature will be used across the entire workflow execution.
                            </DialogDescription>
                        </DialogHeader>

                        <div className="space-y-5">
                            <div className="space-y-2.5">
                                <Label htmlFor="name" className="text-[10px] font-bold text-white/30 uppercase tracking-[0.2em] ml-1">Workflow Identity</Label>
                                <div className="relative group">
                                    <Input
                                        id="name"
                                        value={workflowName}
                                        onChange={(e) => setWorkflowName(e.target.value)}
                                        placeholder="E.G. ALPHA_RECRUIT_GEN_01"
                                        className="bg-white/[0.03] border-white/5 text-white focus:border-indigo-500/40 h-12 text-[13px] rounded-xl transition-all pl-4 group-hover:bg-white/[0.05]"
                                        autoFocus
                                        onKeyDown={(e) => {
                                            if (e.key === 'Enter') handleCreateWorkflow();
                                        }}
                                    />
                                    <div className="absolute inset-0 rounded-xl bg-indigo-500/5 opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none" />
                                </div>
                            </div>
                        </div>

                        <DialogFooter className="flex items-center justify-between sm:justify-between pt-4 border-t border-white/5">
                            <Button 
                                variant="ghost" 
                                onClick={() => setIsModalOpen(false)}
                                className="text-[10px] uppercase tracking-[0.2em] text-white/30 hover:text-white hover:bg-white/5 h-9 px-4 rounded-lg transition-all"
                            >
                                <X size={14} className="mr-2" />
                                Abort
                            </Button>
                            <Button 
                                className="bg-indigo-600 hover:bg-indigo-500 h-9 text-[10px] px-8 rounded-lg shadow-xl shadow-indigo-600/20 uppercase tracking-[0.2em] font-black transition-all hover:scale-[1.02] active:scale-[0.98]"
                                onClick={handleCreateWorkflow}
                            >
                                <Plus size={12} className="mr-2" />
                                Initialize
                            </Button>
                        </DialogFooter>
                    </div>
                </DialogContent>
            </Dialog>
        </div>
    );
};

export default WorkflowLayout;
